import { reactive, onMounted, onBeforeUnmount, ref, watch } from 'vue';
import useMapState from "@/utils/useMapState";
import { useStore } from "vuex";
import chinaMap from './chinaMap.vue';
export default {
  name: 'home',
  components: {
    chinaMap
  },
  setup() {
    // 时间
    let DateTime = new Date();
    var timeID; // 定时器ID
    let timeStr = ref("当前时间：" + DateTime.getFullYear() + 1 + "年" + DateTime.getMonth() + "月" + DateTime.getDate() + "-" + DateTime.getHours() + "时" + DateTime.getMinutes() + "分" + DateTime.getSeconds() + "秒");
    onMounted(() => {
      timeID = setInterval(() => {
        let DateTime = new Date();
        timeStr.value = "当前时间：" + DateTime.getFullYear() + "年" + DateTime.getMonth() + "月" + DateTime.getDate() + "-" + DateTime.getHours() + "时" + DateTime.getMinutes() + "分" + DateTime.getSeconds() + "秒";
      }, 1000);
    });
    onBeforeUnmount(() => {
      clearInterval(timeID);
    });
    // 地图
    const store = useStore();
    const myState = useMapState({
      mapData: state => state.Home.mapData
    });
    onMounted(() => {
      store.dispatch('getMapData');
    });
    return {
      ...myState,
      timeStr
    };
  }
};